
html {
  background: rgb(44, 44, 44);
}

.App {
  color: white
}

a {
  color: #f3f5f7;
  font-weight: bold;
}

#footer {
  font-size: 12px;
  text-align: center;
}

section {
  width: 600px;
  margin: 0 auto;
  margin-top: 100px;
  font-family: "Courier New";
  line-height: 20px;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {

}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
